<template>
  <div>
    <div class="audio__list d-flex flex-column align-center" v-if="audios != []">
      <div v-for="(audio, index) of audios" :key="index">
        <div class="audio__item d-flex flex-row align-center justify-space-between">
          <div class="d-flex">
            <div class="d-flex align-center">
              <v-icon size="64" v-if="music != audio.source" @click="musicPlay(audio)">
                play_arrow
              </v-icon>
              <v-icon size="64" @click="musicPause(audio)" v-if="music == audio.source">
                pause
              </v-icon>
            </div>
            <div class="audio__list__title" v-if="!isEdit || isEdit != audio.source">
              <h2>{{ audio.title }}</h2>
              <p>{{ audio.author }}</p>
            </div>
            <div class="d-flex align-center" v-if="isEdit == audio.source">
              <v-text-field 
                solo 
                class="audio__list__input"
                v-model="sound.title"
              ></v-text-field>
              <v-text-field 
                solo 
                class="audio__list__input"
                v-model="sound.author"
              ></v-text-field>
              <v-btn style="margin-left: 30px" color="success" @click="soundSave(audio)">
                <v-icon>save</v-icon>
              </v-btn>
              <v-btn style="margin-left: 20px" color="error" @click="isEdit = false">
                <v-icon>cancel</v-icon>
              </v-btn>
            </div>
          </div>
          <div 
            v-if="isUser && !isEdit || isEdit != audio.source" 
            class="audio__list__edit d-flex flex-column"
          >
            <v-icon 
              @click="musicDelete(audio)" 
              style="margin-bottom: 30px; cursor: pointer;"
            >
              cancel
            </v-icon>
            <v-icon @click="musicEdit(audio)" style="cursor: pointer;">create</v-icon>
          </div>
        </div>
      </div>
    </div>
    <div style="padding: 50px" v-if="audios.length === 0" class="d-flex flex-row align-center">
      <h2 class="display-1">Вы еще не добавили ни одной аудиозаписи</h2>
      <v-img src="../assets/error.jpg" width="300"></v-img>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "AudioList",
  data() {
    return {
      audios: [],
      music: false,
      audioPlay: "",
      isUser: false,
      user: "",
      isAudio: false,
      isEdit: false,
      sound: {
        title: "",
        author: "",
        source: ""
      },
    }
  },
  async created() {
    let res = await axios.get("/api/users/get_audios/" + this.$route.params.id)
    this.user = JSON.parse(localStorage.getItem("user"))
    if (this.user.id == this.$route.params.id) this.isUser = true
    this.audios = res.data
  },
  methods: {
    musicPlay(audio) {
      if (this.audioPlay) this.audioPlay.pause()
      if (this.isAudio == audio.source) {
        this.music = audio.source
        this.audioPlay.play()
      } else {
        this.music = audio.source
        this.audioPlay = new Audio('/' + audio.source)
        this.audioPlay.play()
      }
    },
    musicPause(audio) {
      this.music = false
      this.isAudio = audio.source
      this.audioPlay.pause()
    },
    async musicDelete(audio) {
      let res = await axios.delete("/api/users/audio-delete/" + this.user.id,
        { params: { audio: audio.source }})
      localStorage.setItem("user", JSON.stringify(res.data))
    },
    musicEdit(audio) {
      this.isEdit = audio.source
      this.sound.title = audio.title
      this.sound.author = audio.author
      this.sound.source = ""
    },
    async soundSave(audio) {
      this.sound.source = audio.source
      let res = await axios.put("/api/users/sound-replace/" + 
        this.$route.params.id, this.sound)
      localStorage.setItem("user", JSON.stringify(res.data))
      this.isEdit = false
    }
  }
};
</script>

<style lang="scss" scoped>
.audio {
  &__list {
    margin-bottom: 30px;
    &__title {
      margin-left: 40px;
      margin-top: 10px;
    }
    &__edit {
      margin-right: 20px;
    }
    &__input {
      margin-left: 30px;
      margin-top: 30px;
    }
  }
  &__item {
    width: 800px;
    min-height: 100px;
    border: 1px solid rgba(0, 0, 0, 0.3);
    margin-top: 30px;
  }
}
</style>
