<template>
  <div>
    <NotAuth>
      <template v-slot:error></template>
      <template v-slot:loading></template>
      <template v-slot:default>
        <div class="audio__wrapper d-flex flex-row">
          <div> <Panel /> </div>
          <v-card class="audio">
            <v-toolbar>
              <v-card-title>Моя музыка</v-card-title>
              <v-spacer></v-spacer>
              <v-btn color="myPurple" style="color: #fff">
                <label for="audioLoad">
                  Добавить аудиозапись
                  <input @input="getAudio" type="file" id="audioLoad" class="hide" />
                </label>
              </v-btn>
            </v-toolbar>
            <AudioList />
          </v-card>
        </div>
      </template>
    </NotAuth>
  </div>
</template>

<script>
import AudioList from "../components/AudioList.vue";
import NotAuth from "../components/NotAuth.vue";
import Panel from "../components/Panel.vue";
import axios from "axios";

export default {
  name: "Audio",
  metaInfo: {
    title: "MyOcean - Мои аудиозаписи"
  },
  components: { AudioList, NotAuth, Panel },
  data() {
    return {
      user: ""
    }
  },
  created() {
    this.user = JSON.parse(localStorage.getItem("user"))
  },
  methods: {
    async getAudio(event) {
      let audio = event.target.files[0]
      let formData = new FormData()
      formData.set("audio", audio, audio.name)
      let res = await axios.put("/api/users/audio_save/" + this.user.id, formData, 
       { headers: { "Content-Type": undefined }})
      localStorage.setItem("user", JSON.stringify(res.data))
    }
  }
};
</script>

<style lang="scss" scoped>
.audio {
  &__wrapper {
    margin-top: 100px;
    margin-left: 30px;
  }
  width: 1170px;
}
.hide {
  position: absolute;
  overflow: hidden;
  z-index: -1;
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
}
</style>
